import VueRouter from 'vue-router'
import Home from './pages/Home'
import Work from './pages/Work'

export default new VueRouter({
  routes: [
    {
      path: '',
      component: Home,
      name: 'Home'
    },
    {
      path: '/work',
      component: Work,
      name: 'Work'
    }
  ],
  mode: 'history',
  scrollBehavior(to, from, savedPosition){

    if(to.hash){
      return {
        selector: to.hash
      }
    }

    return {
      x: 0,
      y: 0
    }
  }
})
